import React, { useContext } from "react";
import { useQuery } from "react-query";
// import { nanoid } from "nanoid";
import { CountryContext } from "../contexts/CountryContext";
import { Card, Row, Col, ListGroup, Container } from "react-bootstrap";

const fetchCountry = async (country) => {
  const response = await fetch(
    `https://restcountries.eu/rest/v2/alpha/${country}`
  );
  const data = await response.json();
  return data;
};

const CountryDetails = () => {
  const { country } = useContext(CountryContext);
  const { data, isLoading, error } = useQuery([country], fetchCountry);

  if (isLoading) return <span>Loading ...</span>;
  if (error) return <span>oop!! error occurred</span>;

  return (
    <Container>
      <Card>
        <Card.Img variant="top" src={data.flag} />
        <Card.Body>
          <Card.Title>{data.name}</Card.Title>
          <Card.Text>
            <Row>
              <Col>Capital: {data.capital}</Col>
              <Col>
                languages:{" "}
                {data.languages.map((lang) => (
                  <ListGroup as="ul">
                    <ListGroup.Item as="li">{lang.name}</ListGroup.Item>
                  </ListGroup>
                ))}
              </Col>
              <Col>
                currencies:{" "}
                {data.currencies.map((curr) => (
                  <ListGroup as="ul">
                    <ListGroup.Item as="li">
                      {curr.name} {curr.symbol}
                    </ListGroup.Item>
                  </ListGroup>
                ))}
              </Col>
            </Row>
          </Card.Text>
        </Card.Body>
      </Card>
    </Container>
  );
};

export default CountryDetails;
